import classes from "./List.module.scss";

interface IProps {
	list: {
		label: string;
		value: string | number;
	}[];
}

const List = ({ list }: IProps) => {
	return (
		<div className={classes.list}>
			{list.map(({ label, value }, key: number) => (
				<div key={key} className={classes.item}>
					<span>{label}</span>
					<strong>{value}</strong>
				</div>
			))}
		</div>
	);
};

export default List;
